<template>
  <div>
    <h1>hello world</h1>
    <div v-for="contact in localContacts" :key="contact.phoneNumber">
      <h3>{{ contact.name }}</h3>
      <p>{{ contact.phoneNumber }}</p>
      <p>{{ contact.message }}</p>
      <p v-if="error" class="error">{{ error }}</p>
    </div>
    <button @click="sendWhatsAppMessage">Send via WhatsApp</button>
    <button @click="startProcessing">Start</button>
    <button @click="reset">Reset</button>
    <button @click="exportToCSV">Export to CSV</button>
    <input type="file" @change="loadCSVFile" accept=".csv">
    <MyTable :contacts="localContacts" />
  </div>
</template>

<script>
import MyTable from './MyTable.vue';
import Papa from 'papaparse'; // Import PapaParse library

export default {
  name: 'ContactList',
    
  components: {
    MyTable
  },
  data() {
    return {
      contacts: [],
   //   localContacts: [...this.contacts], // Create a local copy of contacts
      messageEn: [
        { code: "si", msg: `Hi! This is Alvin Tan from Victova Real Estate. Thank you for your recent inquiry through our Facebook advertisement regarding the Semenyih "Springvale" terrace house. Im here to assist you with any questions you may have. If you are available this weekend, Id be happy to meet you at our showroom to provide further assistance and show you the property. Could we schedule a meeting this Saturday or Sunday at a time that is convenient for you?` },
        { code: "bl", msg: "" }
      ],
      messageCn: [
        { code: "si", msg: `VictovaAlvin TanFacebookSemenyihSpringvale` },
        { code: "bl", msg: "" }
      ],
      messageBs: [
        { code: "si", msg: `Hai! Saya Alvin Tan dari Hartanah Victova. Terima kasih atas pertanyaan anda baru-baru ini melalui iklan Facebook kami mengenai rumah teres "Springvale" di Semenyih. Saya di sini untuk membantu anda dengan sebarang pertanyaan yang anda ada. Jika anda ada masa hujung minggu ini, saya akan dengan senang hati berjumpa dengan anda di bilik pameran kami untuk memberikan bantuan lanjut dan memperkenalkan harta tersebut kepada anda. Bolehkah kita menjadualkan pertemuan pada hari Sabtu atau Ahad ini pada waktu yang sesuai untuk anda?` },
        { code: "bl", msg: "" }
      ],
      currentIndex: 0,
      error: null
    };
  },
  watch: {
    contacts: {
      handler(newContacts) {
        this.localContacts = [...newContacts];
      },
      deep: true,
      immediate: true
    }
  },
  methods: {
    loadCSVFile(event) {
      const file = event.target.files[0];
      if (!file) {
        this.error = 'Please select a file.';
        return;
      }
      const reader = new FileReader();
      reader.onload = (e) => {
        const contents = e.target.result;
        console.log('File contents:', contents);  // Log file contents
        this.parseCSV(contents);
      };
      reader.readAsText(file);
    },
    parseCSV(contents) {
      Papa.parse(contents, {
        header: false, // Assuming no header row
        skipEmptyLines: true, // Skip empty lines
        complete: (results) => {
          if (results.errors.length > 0) {
            console.error('Parsing errors:', results.errors);
            return;
          }
          const parsedContacts = results.data.map(row => ({
            name: row[0]?.trim() || '',
            language: row[1]?.trim() || '',
            phoneNumber: row[2]?.trim() || '',
            status: row[3]?.trim() || '',
            date: row[4]?.trim() || '',
            na1: row[5]?.trim() || '',
            na1desc: row[6]?.trim() || '',
            status1: row[7]?.trim() || '',
            na2: row[8]?.trim() || '',
            na2desc: row[9]?.trim() || '',
            status2: row[10]?.trim() || '',
          }));

          console.log('Parsed contacts:', parsedContacts);
          const numLinesUpdated = parsedContacts.length;
          console.log('Number of lines updated:', numLinesUpdated);
          alert(`Number of lines updated: ${numLinesUpdated}`);

          this.localContacts = parsedContacts; // Update the local copy
          this.$emit('update-contacts', parsedContacts); // Emit event to update parent component
        }
      });
    },
    sendWhatsAppMessage(contact) {
      console.log("Contacts:", this.localContacts); // Check the localContacts array
      const { phoneNumber, message, language, status } = contact;
      if (status !== 'w') {
        this.moveToNextContact();
        return;
      }
      if (this.validateInput(message)) {
        let msg = "";
        if (message.trim() === '@si') {
          if (language.includes("e")) {
            msg += this.messageEn.find(msg => msg.code === "si").msg + "\n\n";
          }
          if (language.includes("c")) {
            msg += this.messageCn.find(msg => msg.code === "si").msg + "\n\n";
          }
          if (language.includes("b")) {
            msg += this.messageBs.find(msg => msg.code === "si").msg;
          }
        }
        navigator.clipboard.writeText(msg);
        const url = `https://api.whatsapp.com/send?phone=${phoneNumber}&text=${encodeURIComponent(msg)}`;
        window.open(url, '_blank');
        this.moveToNextContact();
      }
    },
    validateInput(message) {
      if (!message) {
        this.error = 'Please enter a message.';
        return false;
      }
      this.error = null;
      return true;
    },
    moveToNextContact() {
      if (this.localContacts && this.localContacts.length > 0) {
        do {
          this.currentIndex++;
          if (this.currentIndex >= this.localContacts.length) {
            this.currentIndex = 0; // Reset index to loop through contacts
          }
        } while (this.localContacts[this.currentIndex].status !== 'w');
      }
    },
    startProcessing() {
      this.currentIndex = 0;
      while (this.localContacts[this.currentIndex].status !== 'w') {
        this.moveToNextContact();
      }
    },
    reset() {
      this.currentIndex = 0;
    },
    exportToCSV() {
      const csvContent = this.localContacts.map(contact => {
        console.log(contact.name);
        return `${contact.name},${contact.language},${contact.phoneNumber},${contact.message},${contact.status}`;
      }).join('\n');

      const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
      const link = document.createElement('a');
      const url = URL.createObjectURL(blob);
      link.setAttribute('href', url);
      link.setAttribute('download', 'contacts.csv');
      link.style.visibility = 'hidden';
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    }
  }
};
</script>

<style scoped>
