<template>
  <div class="hello">
    <div>
      <h3>{{ currentContact.name }}</h3>
      <p>{{ currentContact.phoneNumber }}</p>
      <p>{{ currentContact.message }}</p>
      <button @click="sendWhatsAppMessage">Send via WhatsApp2</button>
      <button @click="startProcessing">Start</button>
      <button @click="reset">Reset</button>
      <input type="file" @change="loadCSVFile" accept=".csv">
      <button @click="exportToCSV">Export to CSV</button>
      <p v-if="error" class="error">{{ error }}</p>
    </div>

    <div id="example1">
      <div id="exampleFilterBasicDemo"></div>
    </div>

    <h1>Phone Status</h1>
    <ul class="left-align">
      <li>nr:no response</li>
      <li>nrc:not reachable</li>
      <li>nis:not in services</li>
      <li>cr:can't reach</li>
      <li>hu:hang up</li>
      <li>ab:already bought</li>
      <li>eg:engage</li>
      <li>mb:messagebox</li>
      <li>nt:no dialing tone</li>
      <li>wn:wrong number</li>
      <li>ok: call done</li>
    </ul>
    <h1>Task</h1>
    <ul class="left-align">
      <li>cl:call later</li>
      <li>bl:busy call later</li>
      <li>ap:appointment</li>
      <li>sl:send location info</li>
      <li>wi:want investment properties</li>
    </ul>
  </div>
</template>

<script>
import Papa from 'papaparse'; // Import PapaParse library
import { defineComponent } from 'vue';
import Handsontable from 'handsontable';
import 'handsontable/dist/handsontable.full.min.css';

export default defineComponent({
  name: 'AutoDial',
  data() {
    return {
      messageEn: [
        { code: "si", msg: `Hi! This is Alvin Tan from Victova Real Estate. Thank you for your recent inquiry through our Facebook advertisement regarding the Semenyih "Springvale" terrace house. Im here to assist you with any questions you may have. If you are available this weekend, Id be happy to meet you at our showroom to provide further assistance and show you the property. Could we schedule a meeting this Saturday or Sunday at a time that is convenient for you?` },
        { code: "bl", msg: "" }
      ],
      messageCn: [
        { code: "si", msg: `VictovaAlvin TanFacebookSemenyihSpringvale` },
        { code: "bl", msg: "" }
      ],
      messageBs: [
        { code: "si", msg: `Hai! Saya Alvin Tan dari Hartanah Victova. Terima kasih atas pertanyaan anda baru-baru ini melalui iklan Facebook kami mengenai rumah teres "Springvale" di Semenyih. Saya di sini untuk membantu anda dengan sebarang pertanyaan yang anda ada. Jika anda ada masa hujung minggu ini, saya akan dengan senang hati berjumpa dengan anda di bilik pameran kami untuk memberikan bantuan lanjut dan memperkenalkan harta tersebut kepada anda. Bolehkah kita menjadualkan pertemuan pada hari Sabtu atau Ahad ini pada waktu yang sesuai untuk anda?` },
        { code: "bl", msg: "" }
      ],
      contacts: [{name:"ssss",phoneNumber:"+60116331800"}],
      currentIndex: 0,
      error: null
    }
  },
  computed: {
    currentContact() {
      return this.contacts[this.currentIndex] || {};
    }
  },
  methods: {
    sendWhatsAppMessage() {
      const { phoneNumber, message, language, status } = this.currentContact;
      if (status !== 'w') {
        this.moveToNextContact();
        return;
      }

      if (this.validateInput(message)) {
        let msg = "";
        if (message.trim() === '@si') {
          if (language.includes("e")) {
            msg += this.messageEn.find(msg => msg.code === "si").msg + "\n\n";
          }
          if (language.includes("c")) {
            msg += this.messageCn.find(msg => msg.code === "si").msg + "\n\n";
          }
          if (language.includes("b")) {
            msg += this.messageBs.find(msg => msg.code === "si").msg;
          }
        }
        navigator.clipboard.writeText(msg);
        const url = `https://api.whatsapp.com/send?phone=${phoneNumber}&text=${encodeURIComponent(msg)}`
        window.open(url, '_blank');
        this.moveToNextContact();
      }
    },
    validateInput(message) {
      if (!message) {
        this.error = 'Please enter a message.';
        return false;
      }
      this.error = null;
      return true;
    },
    moveToNextContact() {
      do {
        this.currentIndex++;
        if (this.currentIndex >= this.contacts.length) {
          this.currentIndex = 0; // Reset index to loop through contacts
        }
      } while (this.contacts[this.currentIndex].status !== 'w');
    },
    startProcessing() {
      this.currentIndex = 0;
      while (this.contacts[this.currentIndex].status !== 'w') {
        this.moveToNextContact();
      }
    },
    reset() {
      this.currentIndex = 0;
    },
    loadCSVFile(event) {
      const file = event.target.files[0];
      if (!file) {
        this.error = 'Please select a file.';
        return;
      }
      const reader = new FileReader();
      reader.onload = (e) => {
        const contents = e.target.result;
        console.log('File contents:', contents);  // Log file contents
        this.parseCSV(contents);
      };
      reader.readAsText(file);
    },
    parseCSV(contents) {
      Papa.parse(contents, {
        header: false, // Assuming no header row
        skipEmptyLines: true, // Skip empty lines
        complete: (results) => {
          if (results.errors.length > 0) {
            // Handle parsing errors
            console.error('Parsing errors:', results.errors);
            return;
          }

          // Skip the first row assuming it is the header
          const parsedContacts = results.data.slice(0).map(row => ({
            name: row[0]?.trim() || '',
            language: row[1]?.trim() || '',
            phoneNumber: row[2]?.trim() || '',
            status: row[3]?.trim() || '',
            date: row[4]?.trim() || '',
            na1: row[5]?.trim() || '',
            na1desc: row[6]?.trim() || '',
            status1: row[7]?.trim() || '',
            na2: row[8]?.trim() || '',
            na2desc: row[9]?.trim() || '',
            status2: row[10]?.trim() || '',
          }));

          console.log('Parsed contacts:', parsedContacts);
          const numLinesUpdated = parsedContacts.length;
          console.log('Number of lines updated:', numLinesUpdated);
          alert(`Number of lines updated: ${numLinesUpdated}`);

          console.log('Contacts before update:', this.contacts);
          this.contacts = parsedContacts;
          console.log('Contacts after update:', this.contacts);

          this.updateTable(parsedContacts);
        }
      });
    },
    updateTable(data) {
      const container = document.querySelector('#exampleFilterBasicDemo');
      if (container.handsontableInstance) {
        container.handsontableInstance.loadData(data);
      } else {
        container.handsontableInstance = new Handsontable(container, {
          data,
          columns: [
            {
              title: 'Name',
              type: 'text',
              data: 'name',
            },
            {
              title: 'Language',
              type: 'text',
              data: 'language',
            },
            {
              title: 'Phone Number',
              type: 'text',
              data: 'phoneNumber',
            },
            {
              title: 'Status',
              type: 'text',
              data: 'status',
            },
            {
              title: 'Date',
              type: 'text',
              data: 'date',
            },
            {
              title: 'Next Action 1',
              type: 'text',
              data: 'na1',
            },
            {
              title: 'Next Action 1 Desc',
              type: 'text',
              data: 'na1desc',
            },
            {
              title: 'Status 1',
              type: 'text',
              data: 'status1',
            },
            {
              title: 'Next Action 2',
              type: 'text',
              data: 'na2',
            },
            {
              title: 'Next Action 2 Desc',
              type: 'text',
              data: 'na2desc',
            },
            {
              title: 'Status 2',
              type: 'text',
              data: 'status2',
            },
            
          ],
          filters: true,
          dropdownMenu: true,
          height: 'auto',
          autoWrapRow: true,
          autoWrapCol: true,
          licenseKey: 'non-commercial-and-evaluation',
        });
      }
    },
    exportToCSV() {
      const csvContent = this.contacts.map(contact => {
        return `${contact.name},${contact.language},${contact.phoneNumber},${contact.status},${contact.date},${contact.na1},${contact.na1desc},${contact.status1},${contact.na2},${contact.na2desc},${contact.status2}`;
      }).join('\n');

      const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' });
      const link = document.createElement('a');
      const url = URL.createObjectURL(blob);
      link.setAttribute('href', url);
      link.setAttribute('download', 'contacts.csv');
      link.style.visibility = 'hidden';
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    }
  },
  mounted() {
    this.updateTable(this.contacts);
  }
});
</script>

<style>
.hello {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.left-align {
  text-align: left;
}

#error {
  color: red;
}

#example1 {
  margin: 20px;
}
</style>
